import React, { useCallback, useState } from "react";
import getCroppedImg from "../../../components/getCroppedImage";
import Loading from "../../../components/loading";
import Cropper from "react-easy-crop";
import axios from "axios";
import { upload_image } from "../../../utils/end-points";
import { useSelector } from "react-redux";
import Parser from "html-react-parser";

function Upload({ stepStep }) {
  //redux state
  const data = useSelector((data) => data.data);

  const [file, setFile] = useState(null);
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);
  const [loading, setLoading] = useState(false);
  const [errosMessage, setErrorMessage] = useState("");
  const [croppedArea, setCroppedArea] = useState(null);

  // upload Image Onchange
  const uploadImageHandler = (e) => {
    setLoading(true);
    setFile(URL.createObjectURL(e.target.files[0]));
    setLoading(false);
    if (file) {
      setErrorMessage("");
    }
  };

  //  Call back Function On completing Crop

  const onCropComplete = useCallback((croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  }, []);

  // crop image and post image Function

  const showCroppedImage = useCallback(
    async (e) => {
      e.preventDefault();
      setLoading(true);
      try {
        if (file === null) {
          setErrorMessage("Please select Profile Image");
        } else {
          setErrorMessage("");
          const croppedImage = await getCroppedImg(file, croppedAreaPixels);

          const formData = new FormData();
          const userId = localStorage.getItem("userId");
          formData.append("images", croppedImage.file);
          formData.append("user_id", userId);
          await axios
            .post(upload_image, formData)
            .then((res) => {
              stepStep();
              localStorage.setItem("uploadWidth", "16.6%");
            })
            .catch((err) => {});
          stepStep();
        }
      } catch (e) {
        {
        }
      }
      setLoading(false);
    },
    [croppedAreaPixels]
  );

  // Get User from LocalStorage

  const user = JSON.parse(localStorage.getItem("user"));

  //style for Progress bar

  const styles = {
    role: {
      backgroundColor: user?.roleId === "mentor" ? "#1790FF" : "#fdd417",
      width: file ? "16.6%" : "5%",
      color: user?.roleId === "mentor" ? "#fff" : "#000",
    },
  };

  return (
    <form onSubmit={showCroppedImage}>
      {data?.signupData?.add_profile_photo_title
        ? Parser(data?.signupData?.add_profile_photo_title)
        : ""}

      <p className="p-default text_para_second">
        {data?.signupData?.add_profile_photo_description
          ? Parser(data?.signupData?.add_profile_photo_description)
          : ""}
      </p>
      <div className="progress">
        <div
          className="progress-bar-profile"
          role="progressbar"
          aria-label="Example with label"
          style={styles.role}
          aria-valuenow={file ? "16.6%" : "0%"}
          aria-valuemin="0"
          aria-valuemax="100"
        >
          {file ? "16.6%" : "0%"}
        </div>
      </div>

      {!file ? (
        <>
          <label htmlFor="profile-photo" className="profile-photo-trigger">
            <div className="row justify-content-center text-center">
              <div className="col-8">
                <img src="./images/plus-icon.svg" alt="" />
                <p className="small-text text-capitalize mt-3 mb-0">
                  Darg and drop or upload a photo that Best represents your
                  profile
                </p>
              </div>
            </div>
            <input
              type="file"
              name="file"
              accept="image/png, image/gif, image/jpeg"
              className="profile-photo"
              id="profile-photo"
              onChange={uploadImageHandler}
            />
          </label>
          <div className="error_new">
            {errosMessage && (
              <span className={"error_message"}>{errosMessage}</span>
            )}
          </div>
        </>
      ) : (
        <>
          <label htmlFor="profile-photo" className="profile-photo-trigger">
            <button
              type="button"
              onClick={() => {
                setFile(null);
              }}
              className="del-btn bg-black"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                width="24"
                height="24"
              >
                <path fill="none" d="M0 0h24v24H0z" />
                <path
                  fill="#fff"
                  id="outline"
                  d="M17 6h5v2h-2v13a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V8H2V6h5V3a1 1 0 0 1 1-1h8a1 1 0 0 1 1 1v3zm1 2H6v12h12V8zm-9 3h2v6H9v-6zm4 0h2v6h-2v-6zM9 4v2h6V4H9z"
                />
              </svg>
            </button>
            <div className="justify-content-center text-center">
              <div className="">
                {/* Npm React Easy Crop  */}
                <Cropper
                  image={file}
                  crop={crop}
                  zoom={zoom}
                  aspect={4 / 3}
                  onCropChange={setCrop}
                  onCropComplete={onCropComplete}
                  onZoomChange={setZoom}
                  cropShape={"round"}
                  cropSize={{ width: 300, height: 300 }}
                  showGrid={false}
                  objectFit="auto-cover"
                  onCropAreaChange={(croppedArea) => {
                    setCroppedArea(croppedArea);
                  }}
                />
              </div>
            </div>
            <div className="error_new">
              {errosMessage && (
                <span className={"error_message"}>{errosMessage}</span>
              )}
            </div>
          </label>
        </>
      )}

      <div>
        <button
          type="submit"
          style={{ color: `#fff` }}
          className="upload-btn btn  w-100 next-slide"
        >
          {loading === true ? (
            <Loading loaderClass="loading_button" />
          ) : (
            "Upload Photo"
          )}
        </button>
      </div>
    </form>
  );
}

export default Upload;
